import './Button.scss'; 

const isOperator = (value) => {
    return !isNaN(value) || value === '.' || value === '='; // nu ng l s hoc du chm hoc du bng th tr v true. hm ny  xc nh cc ton t  CSS
}

export const Button = (props) => {
    return(
        <div 
            className={`btn-wrapper ${isOperator(props.children) ? null : 'operator'}`}
            onClick={() => props.handleClick(props.children, 123, 124)}
        >
            {props.children}
        </div>
    )
}
